<template>
  <table-component :data="blocks" sort-by="height" sort-order="desc" :show-filter="false" :show-caption="false" table-class="w-full">
    <table-column show="id" label="ID" header-class="p-4 pl-10 text-left" cell-class="p-4 pl-10 text-left border-none">
      <template slot-scope="row">
        <link-block :id="row.id"></link-block>
      </template>
    </table-column>

    <table-column show="height" label="Height" header-class="p-4 text-left hidden md:table-cell" cell-class="p-4 text-left border-none hidden md:table-cell">
      <template slot-scope="row">
        {{ row.height }}
      </template>
    </table-column>

    <table-column show="timestamp" label="Timestamp" header-class="p-4 text-left" cell-class="p-4 text-left border-none">
      <template slot-scope="row">
        {{ readableTimestamp(row.timestamp) }}
      </template>
    </table-column>

    <table-column show="numberOfTransactions" label="Transactions" header-class="p-4 text-right hidden md:table-cell" cell-class="p-4 text-right hidden md:table-cell border-none">
      <template slot-scope="row">
        {{ row.numberOfTransactions }}
      </template>
    </table-column>

    <table-column show="generatorPublicKey" label="Generated by" header-class="p-4 text-right hidden lg:table-cell" cell-class="p-4 text-right border-none hidden lg:table-cell">
      <template slot-scope="row">
        <link-wallet :public-key="row.generatorPublicKey"></link-wallet>
      </template>
    </table-column>

    <table-column show="totalForged" label="Reward (ARK)" header-class="p-4 text-right" cell-class="p-4 text-right border-none">
      <template slot-scope="row">
        {{ readableCrypto(row.totalForged) }}
      </template>
    </table-column>

    <table-column show="totalFee" label="Fee (ARK)" header-class="p-4 pr-10 text-right" cell-class="p-4 pr-10 text-right border-none">
      <template slot-scope="row">
        {{ readableCrypto(row.totalFee) }}
      </template>
    </table-column>
  </table-component>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    blocks: {
      type: Array,
      required: true,
    },
  }
}
</script>



